import React,{useState} from 'react'
import{menuItemModel,apiResponse}from'../../../interfaces';
import{NavLink,Link}from'react-router-dom';
import{useUpdateShoppingCartMutation}from'../../../apis';
import{MiniLoader}from'../Common';
import{toastNotify}from'../../../Helper';
import{useSelector}from'react-redux';
import{RootState}from'../../../Storage';
import{userModel}from'../../../interfaces';
import{useNavigate}from'react-router-dom'


export interface MenuItemCardProps {
    menuItem:menuItemModel
}

export const MenuItemCard=(props:MenuItemCardProps)=>{
  const navigate=useNavigate();
  const [updateShoppingCart]=useUpdateShoppingCartMutation()
  const[isAddingToCart,setIsAddingToCart]=useState<boolean>(false)
  const userData:userModel=useSelector((state:RootState)=>state.userAuthStore)

  const handleAddToCart=async (menuItemId:number)=>{
    if(!userData.id){
      navigate('/login')
      return
    }
    setIsAddingToCart(true);
    const response:apiResponse= await updateShoppingCart({
      menuItemId:menuItemId,
      updateQuantityBy:1,
      userId:userData.id
    })
   if(response.data&&response.data.isSuccess){
    toastNotify('Item added to cart successfully!')
   }
    setIsAddingToCart(false)
  }

return (
 
    <div className="col-md-4 col-12 p-4">
      <div
        className="card"
        style={{ boxShadow: "0 1px 7px 0 rgb(0 0 0 / 50%)" }}
      >
        <div className="card-body pt-2">
            <Link to={`/menuItemDetails/${props.menuItem.id}`}>
                   <div className=" row col-10 offset-1 p-4" style={{height:'250px'}}>
       
                    <img
                        src={props.menuItem.image}
                        style={{ borderRadius: "50%" }}
                        alt=""
                        className="w-100 mt-5 image-box"
                        /> 
                    
                    
                    </div>
            </Link>
       
{props.menuItem.specialTag&&props.menuItem.specialTag.length>0&&(
       <i
            className="bi bi-star btn btn-success"
            style={{
              position: "absolute",
              top: "15px",
              left: "15px",
              padding: "5px 10px",
              borderRadius: "3px",
              outline: "none !important",
              cursor: "pointer",
            }}
          >
            &nbsp; {props.menuItem.specialTag}
          </i>
)}
       
            {isAddingToCart?(<div style={{
              position:"absolute",
              top:"15px",
              right:"15px"
            }}><MiniLoader type={"danger"} size={50}/></div>):(   <i
            className="bi bi-cart-plus btn btn-outline-danger"
            style={{
              position: "absolute",
              top: "15px",
              right: "15px",
              padding: "5px 10px",
              borderRadius: "3px",
              outline: "none !important",
              cursor: "pointer",
            }}
            onClick={()=>handleAddToCart(props.menuItem.id)}
          ></i>)}
       

          <div className="text-center">
            <p className="card-title m-0 text-success fs-3"><Link 
            to={`/menuItemDetails/${props.menuItem.id}`}
            style={{textDecoration:'none',color:'green'}}
            >{props.menuItem.name}</Link></p>
            <p className="badge bg-secondary" style={{ fontSize: "12px" }}>
              {props.menuItem.category}
            </p>
          </div>
          <p className="card-text" style={{ textAlign: "center" }}>
            {props.menuItem.description}
          </p>
          <div className="row text-center">
            <h4>${props.menuItem.price}</h4>
          </div>
        </div>
      </div>
    </div>
  
);
}
