import React from 'react'
import{useState,useEffect}from'react';
import{menuItemModel}from'../../../interfaces';
import{MenuItemCard}from'./MenuItemCard';
import{useGetMenuItemsQuery}from'../../../apis';

import{useDispatch, useSelector}from'react-redux';
import{RootState, setMenuItem}from'../../../Storage';
import{MainLoader}from'../Common';
import { SD_SortTypes } from '../../../Utility/SD';


// export interface MenuItemListProps {}
// export const MenuItemList=({}:MenuItemListProps)=>(

export const MenuItemList=()=>{
  const [menuItems, setMenuItems] = useState<menuItemModel[]>([]);
  const [selectedCategory,setSelectedCategory]=useState('All')
  const[categoryList,setCategoryList]=useState([''])
  const dispatch=useDispatch();

  const[sortName,setSortName]=useState(SD_SortTypes.NAME_A_Z)

  const{data,isLoading}=useGetMenuItemsQuery(null)

  const sortOptions:Array<SD_SortTypes>=[
    SD_SortTypes.PRICE_LOW_HIGH,
    SD_SortTypes.PRICE_HIGH_LOW,
    SD_SortTypes.NAME_A_Z,
    SD_SortTypes.NAME_Z_A,
  ]

  const searchValue=useSelector(
    (state:RootState)=>state.menuItemStore.search
  )
  useEffect(() => {
    if(data&&data.result){
      const tempMenuArray=handleFilters(sortName,selectedCategory,searchValue)
      setMenuItems(tempMenuArray)

  
    }
  }, [searchValue]);

  useEffect(() => {
    if(!isLoading){
      
      dispatch(setMenuItem(data.result))
      setMenuItems(data.result)
      const tempCategoryList=['All'];
      data.result.forEach((item:menuItemModel)=>{
        if(tempCategoryList.indexOf(item.category)===-1){
          tempCategoryList.push(item.category)
        }
      })
      
      setCategoryList(tempCategoryList)

    }
  }, [isLoading]);

  const handleSortClick=(i:number)=>{
    setSortName(sortOptions[i])
    const tempArray=handleFilters(
      sortOptions[i],
      selectedCategory,
      searchValue
    )
    setMenuItems(tempArray)
  }

  const handleCategoryClick = (i: number) => {
    const buttons = document.querySelectorAll(".custom-buttons");
    let localCategory;
    buttons.forEach((bt, index) => {
      if (index === i) {
        bt.classList.add("active");
        if (index === 0) {
          localCategory = "All";
        } else {
          localCategory = categoryList[index];
        }
        console.log(localCategory)
        setSelectedCategory(localCategory);
        const tempArray=handleFilters(sortName,localCategory,searchValue)
        setMenuItems(tempArray)
      } else {
        bt.classList.remove("active");
      }
      
    });
  };

const handleFilters=(sortType:SD_SortTypes,category:string,search:string)=>{
  let tempArray=category==='All'?[...data.result]:
  data.result.filter((it:menuItemModel)=>
    it.category.toUpperCase()===category.toUpperCase()
  )
  if(search){
    const tempArray2 = [...tempArray];
    tempArray = tempArray2.filter((item: menuItemModel) =>
      item.name.toUpperCase().includes(search.toUpperCase()))
  }
  if(sortType===SD_SortTypes.PRICE_LOW_HIGH){
    tempArray.sort((a:menuItemModel,b:menuItemModel)=>a.price-b.price)
  }
  if(sortType===SD_SortTypes.PRICE_HIGH_LOW){
    tempArray.sort((a:menuItemModel,b:menuItemModel)=>b.price-a.price)
  }
  if(sortType===SD_SortTypes.NAME_A_Z){
    tempArray.sort((a:menuItemModel,b:menuItemModel)=>
    a.name.toUpperCase().charCodeAt(0)-
    b.name.toUpperCase().charCodeAt(0))
  }
  if(sortType===SD_SortTypes.NAME_Z_A){
    tempArray.sort((a:menuItemModel,b:menuItemModel)=>
     b.name.toUpperCase().charCodeAt(0)-
    a.name.toUpperCase().charCodeAt(0))
   
  }
  return tempArray
}

  if(isLoading){
    return <MainLoader/>
  }

  return (
    
    <div className="container row">
      <div className="my-3">
        <ul className="nav w-100 d-flex justify-content-center">
          {categoryList.map((item:string,index)=>(<li 
          className='nav-item'
          style={{...(index===0&&{marginLeft:'auto'})}} 
          key={index} >
            <button 
            className={`nav-link p-0 pb-2 custom-buttons fs-5 ${index===0&&"active"}`}
            onClick={()=>handleCategoryClick(index)}
            >
              {item}
            </button>
          </li>))}
          <li className="nav-item dropdown" style={{marginLeft:'auto'}}>
            <div className="nav-link dropdown-toggle text-dark fs-6 border"
            role='button'
            data-bs-toggle='dropdown'
            aria-expanded='false'
            >
              {sortName}
            </div>
            <ul className='dropdown-menu'>
              {sortOptions.map(((it,index)=>(<li key={index}
              className='dropdown-item'
              onClick={()=>handleSortClick(index)}
              >{it}</li>)))}
            </ul>
          </li>
        </ul>
      </div>
      
       {menuItems.length > 0 &&
        menuItems.map((it:menuItemModel, index:number) => (
        <MenuItemCard menuItem={it} key={index} />
        )
       )} 
    </div>
  );
}
