import{toast,TypeOptions}from'react-toastify';

export const toastNotify=(message:string,type:TypeOptions="success")=>{
    toast(message, {
        type:type,
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "dark",
        });
}